import React, { useState } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

import CardList from './components/card-list/card-list.component';
import SearchBox from './components/search-box/search-box.component';
import './App.css';



import '@ionic/react/css/core.css';
import { setupIonicReact } from '@ionic/react';
import { IonButton, IonDatetime } from '@ionic/react';
import { IonButtons, IonContent, IonHeader, IonMenu, IonMenuButton, IonPage, IonTitle, IonToolbar, IonList, IonItem,IonGrid, IonRow, IonCol } from '@ionic/react';


/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

setupIonicReact();

const initialCurrencyPairs = [
  { id: '1', pair: 'EUR/USD', buy: 1.18, sell: 1.19 },
  { id: '2', pair: 'USD/JPY', buy: 109.76, sell: 109.78 },
  { id: '3', pair: 'GBP/USD', buy: 1.39, sell: 1.40 },
  { id: '4', pair: 'AUD/USD', buy: 0.73, sell: 0.74 },
  { id: '5', pair: 'USD/CHF', buy: 0.92, sell: 0.93 },
  { id: '6', pair: 'USD/CAD', buy: 1.24, sell: 1.26 },
  { id: '7', pair: 'EUR/JPY', buy: 129.9, sell: 130.1 },
  { id: '8', pair: 'EUR/GBP', buy: 0.85, sell: 0.86 },
  { id: '9', pair: 'EUR/CHF', buy: 1.09, sell: 1.11 },
  { id: '10', pair: 'GBP/JPY', buy: 152.4, sell: 152.6 }
];

// fake data generator
const getItems = count =>
  Array.from({ length: count }, (v, k) => k).map(k => ({
    id: `item-${k}`,
    content: `item ${k}`
  }));

// a little function to help us with reordering the result
const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

const grid = 8;

const getItemStyle = (isDragging, draggableStyle) => ({
  // some basic styles to make the items look a bit nicer
  userSelect: "none",
  padding: grid * 2,
  margin: `0 0 ${grid}px 0`,

  // change background colour if dragging
  background: isDragging ? "lightgreen" : "grey",

  // styles we need to apply on draggables
  ...draggableStyle
});

const getListStyle = isDraggingOver => ({
  background: isDraggingOver ? "lightblue" : "lightgrey",
  padding: grid,
  width: 250
});






const  App = () => {
  console.log('render');
  const [currencyPairs, setCurrencyPairs] = useState(initialCurrencyPairs);


  const onDragEnd = (result) => {
    if (!result.destination) return;

    const newCurrencyPairs = Array.from(currencyPairs);
    const [reorderedItem] = newCurrencyPairs.splice(result.source.index, 1);
    newCurrencyPairs.splice(result.destination.index, 0, reorderedItem);

    setCurrencyPairs(newCurrencyPairs);
  }
  
  return (
      
    <div className="App">
      <h1 className='app-title'>Execute Sample</h1>

      <IonMenu contentId="main-content">
        <IonHeader>
          <IonToolbar>
            <IonTitle>Menu Content</IonTitle>
          </IonToolbar>
        </IonHeader>
        
    <IonContent>
      <IonList>
        <IonItem className="menu-item">Menu Item 1</IonItem>
        <IonItem className="menu-item">Menu Item 2</IonItem>
        <IonItem className="menu-item">Menu Item 3</IonItem>
      </IonList>
    </IonContent>
        
      </IonMenu>
      <IonPage id="main-content">
        <IonHeader>
          <IonToolbar>
            <IonButtons slot="start">
              <IonMenuButton></IonMenuButton>
            </IonButtons>
            <IonTitle>Menu</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent className="ion-padding">Tap the button in the toolbar to open the menu.



<IonGrid>
      <IonRow>
        <IonCol><strong>Currency Pair</strong></IonCol>
        <IonCol><strong>Buy Price</strong></IonCol>
        <IonCol><strong>Sell Price</strong></IonCol>
      </IonRow>


<DragDropContext onDragEnd={onDragEnd}>
   <Droppable droppableId="droppable">
          {(provided) => (
            <div
              {...provided.droppableProps}
              ref={provided.innerRef}
            >


                            {currencyPairs.map((currencyPair, index) => (
                              
                <Draggable key={currencyPair.id} draggableId={'GAVIN' + currencyPair.id} index={index}>
                  {(provided, snapshot) => (
                    
                    <IonRow
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}
                    style={getItemStyle(
                        snapshot.isDragging,
                        provided.draggableProps.style
                      )}>
                      <IonCol>{currencyPair.pair}</IonCol>
                      <IonCol>{currencyPair.buy.toFixed(5)}</IonCol>
                      <IonCol>{currencyPair.sell.toFixed(5)}</IonCol>
                    </IonRow>
                  )}
                </Draggable>
              ))}


              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>


    </IonGrid>
        </IonContent>
      </IonPage>
    </div >
  );

}

export default App;
